import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/LoginPage.vue'
import Dashboard from '../components/pages/Dashboard.vue'
import Profiles from '../components/pages/Profiles.vue'
import Analytics from '../components/pages/Analytics.vue'
import Crops from '../components/pages/Crops.vue'

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/home',
    component: Home,
    children: [
      {
        path: '',
        name: 'home-dashboard',
        component: Dashboard
      },
      {
        path: 'profiles',
        component: Profiles
      },
      {
        path: 'analytics',
        component: Analytics
      },
      {
        path: 'crops',
        component: Crops
      }
    ]
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
