<template>
    <div class="search-bar-widget">
        <div class="search-bar">
            <div class="search-icon-section">
                <span class="material-icons" style="font-size:1.3rem">search</span>
            </div>
            <div class="search-input-section">
                <input type="text" name="search" v-model="searchString" v-on:keydown="keyPress" id="searchbar" v-bind:placeholder="placeholder" class="search-input">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Searchbar',
    props: ['placeholder'],
    data() {
        return {
            searchString: ""
        }
    },
    methods: {
        keyPress:function(event) {
            setTimeout(() => {
                if(this.isalnum(event.key)) {
                    this.$emit('search-item', this.searchString)
                }
            }, 50)
        },
        isalnum:function(c) {
            return ((((c >= 'a') && (c <= 'z')) || ((c >= 'A') && (c <= 'Z'))) || ((c >= '0') && (c <= '9')))
        }
    }
}
</script>

<style scoped>
    .search-bar{
        display:flex;
        /* align-items: center; */
        padding:10px 30px;
        background:white;
    }

    .search-input-section{
        width:100%;
    }

    .search-input{
        width:100%;
        background-color: transparent;
        border:none;
        font-family:"Montserrat";
        outline:none;
        margin-left:30px;
        font-size:0.8rem;
    }
</style>